// Core
import React, { Component } from 'react';
import Move from 'react-flip-move';

// Instruments
import Styles, { Kit } from './styles';
import Checkbox from '../../theme/assets/Checkbox';
import { url, token } from '../../config/api';
import { sortTasksByGroup } from '../../instruments';

// Components
import Task from '../Task';
import Spinner from '../Spinner';

export default class Scheduler extends Component {
    state = {
        newTaskMessage:  '',
        tasksFilter:     '',
        isTasksFetching: false,
        tasks:           [],
    };

    componentDidMount () {
        this._fetchTasksAsync();
    }

    _updateTasksFilter = (event) => {
        this.setState({
            tasksFilter: event.target.value.toLowerCase(),
        });
    };

    _setTasksFetchingState = (state) => {
        this.setState({
            isTasksFetching: state,
        });
    };

    _fetchTasksAsync = async () => {
        try {
            this._setTasksFetchingState(true);

            const response = await fetch(url, {
                method:  'GET',
                headers: {
                    Authorization: token,
                },
            });

            const { data: tasks, message } = await response.json();

            if (response.status !== 200) {
                throw new Error(message);
            }

            this.setState({
                tasks:           sortTasksByGroup(tasks),
                isTasksFetching: false,
            });
        } catch (error) {
            console.log('tasks was not loaded', error);
            this._setTasksFetchingState(false);
        }
    };

    _createTaskAsync = async (event) => {
        try {
            event.preventDefault();
            const { newTaskMessage } = this.state;

            if (!newTaskMessage) {
                return null;
            }

            this._setTasksFetchingState(true);

            const response = await fetch(url, {
                method:  'POST',
                headers: {
                    Authorization:  token,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ message: newTaskMessage }),
            });

            const { data: task, message } = await response.json();

            if (response.status !== 200) {
                throw new Error(message);
            }

            this.setState(({ tasks }) => ({
                tasks:           sortTasksByGroup([task, ...tasks]),
                newTaskMessage:  '',
                isTasksFetching: false,
            }));
        } catch (error) {
            console.log('task was not created', error);
        }
    };

    _updateTask = async (updatedTask) => {
        try {
            this._setTasksFetchingState(true);

            const response = await fetch(url, {
                method:  'PUT',
                headers: {
                    Authorization:  token,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify([updatedTask]),
            });

            const {
                data: [taskFromResponse],
                message: errorMessage,
            } = await response.json();

            if (response.status !== 200) {
                throw new Error(errorMessage);
            }

            this.setState(({ tasks }) => {
                const indexToReplace = tasks.indexOf(
                    tasks.find((task) => task.id === updatedTask.id),
                );

                const newTasks = [
                    ...tasks.filter((task) => task.id !== updatedTask.id)
                ];

                newTasks.splice(indexToReplace, 0, taskFromResponse);

                const sortedTasks = sortTasksByGroup(newTasks);

                return {
                    tasks:           sortedTasks,
                    isTasksFetching: false,
                };
            });
        } catch (error) {
            console.log('task was not created', error);
        }
    };

    _removeTask = async (id) => {
        try {
            this._setTasksFetchingState(true);

            const response = await fetch(`${url}/${id}`, {
                method:  'DELETE',
                headers: {
                    Authorization: token,
                },
            });

            if (response.status !== 204) {
                const { message: errorMessage } = await response.json();

                throw new Error(errorMessage);
            }

            this.setState(({ tasks }) => ({
                tasks:           tasks.filter((task) => task.id !== id),
                isTasksFetching: false,
            }));
        } catch (error) {
            console.log('task was not deleted', error);
        }
    };

    _completeAll = async () => {
        try {
            if (this._getAllCompleted()) {
                return null;
            }

            const promises = [];

            for (const task of this.state.tasks) {
                promises.push(
                    fetch(url, {
                        method:  'PUT',
                        headers: {
                            Authorization:  token,
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify([{ ...task, completed: true }]),
                    }),
                );
            }

            const responses = await Promise.all(promises);
            const success = responses.every((result) => result.status === 200);

            if (!success) {
                throw new Error('Posts was not completed');
            }

            this.setState(({ tasks }) => ({
                tasks: sortTasksByGroup(
                    tasks.map((task) => ({ ...task, completed: true })),
                ),
                isTasksFetching: false,
            }));
        } catch (error) {
            console.log('✓ all tasks was not completed:', error);
        }
    };

    _updateNewTaskMessage = (event) => {
        const newTaskMessage = event.target.value;

        newTaskMessage.length < 50 &&
            this.setState({
                newTaskMessage,
            });
    };

    _updateTaskFilter = (event) => {
        this.setState({
            taskFilter: event.target.value.toLowerCase(),
        });
    };

    _getAllCompleted = () => this.state.tasks.every((task) => task.completed);

    render () {
        const {
            tasks,
            newTaskMessage,
            tasksFilter,
            isTasksFetching,
        } = this.state;

        const allCompleted = this._getAllCompleted();
        const todoList = tasks
            .filter((task) => task.message.toLowerCase().includes(tasksFilter))
            .map((props) => (
                <Task
                    _removeTask = { this._removeTask }
                    _updateTask = { this._updateTask }
                    key = { props.id }
                    { ...props }
                />
            ));

        return (
            <section className = { Styles }>
                <Spinner isSpinning = { isTasksFetching } />
                <main>
                    <header>
                        <h1 className = { Kit.test }>Планировщик задач</h1>
                        <input
                            placeholder = 'Поиск'
                            type = 'search'
                            value = { tasksFilter }
                            onChange = { this._updateTasksFilter }
                        />
                    </header>
                    <section>
                        <form onSubmit = { this._createTaskAsync }>
                            <input
                                className = { Kit.createTask }
                                placeholder = 'Описание моей новой задачи'
                                type = 'text'
                                value = { newTaskMessage }
                                onChange = { this._updateNewTaskMessage }
                            />
                            <button>Добавить задачу</button>
                        </form>
                        <div className = { Kit.overlay }>
                            <ul>
                                <Move duration = { 400 } easing = 'ease-in-out'>
                                    {todoList}
                                </Move>
                            </ul>
                        </div>
                    </section>
                    <footer>
                        <Checkbox
                            checked = { allCompleted }
                            color1 = '#363636'
                            color2 = '#fff'
                            onClick = { this._completeAll }
                        />
                        <span className = { Kit.completeAllTasks }>
                            Все задачи выполнены
                        </span>
                    </footer>
                </main>
            </section>
        );
    }
}
