// Core
import React, { PureComponent, createRef } from 'react';
import cx from 'classnames';

// Instruments
import Styles, { Kit } from './styles';

// Components
import Checkbox from '../../theme/assets/Checkbox';
import Remove from '../../theme/assets/Remove';
import Edit from '../../theme/assets/Edit';
import Star from '../../theme/assets/Star';

export default class Task extends PureComponent {
    // static getDerivedStateFromProps (prevProps, nextState) {
    //     if (prevProps.message !== nextState.newMessage) {
    //         return {
    //             newMessage: prevProps.message,
    //         };
    //     }

    //     return null;
    // }

    state = {
        editing:    false,
        newMessage: this.props.message,
    };

    componentDidUpdate (_, { editing }) {
        !editing && this.input.current.focus();
    }

    input = createRef();

    _getTaskShape = ({
        id,
        completed = this.props.completed,
        favorite = this.props.favorite,
        message = this.props.message,
    }) => ({
        id,
        completed,
        favorite,
        message,
    });

    _editNewMessage = (event) => {
        this.setState({
            newMessage: event.target.value,
        });
    };

    _toggleEditingState = () => {
        this.setState(({ editing }) => ({
            editing: !editing,
        }));
    };

    _updateTask = (event) => {
        const { newMessage } = this.state;
        const { _updateTask, id } = this.props;

        if (!newMessage.length) {
            return;
        }

        switch (event.key) {
            case 'Enter': {
                this.setState({
                    editing: false,
                });
                _updateTask(this._getTaskShape({ id, message: newMessage }));

                break;
            }

            case 'Escape': {
                this.setState({
                    newMessage,
                    editing: false,
                });

                break;
            }

            default:
                break;
        }
    };

    _completeTask = () => {
        const { id, _updateTask } = this.props;

        _updateTask(
            this._getTaskShape({ id, completed: !this.props.completed }),
        );
    };

    _changePriority = () => {
        const { id, _updateTask } = this.props;

        _updateTask(this._getTaskShape({ id, favorite: !this.props.favorite }));
    };

    _removeTask = () => {
        const { id, _removeTask } = this.props;

        _removeTask(id);
    };

    render () {
        const { editing, newMessage } = this.state;
        const { message, completed, favorite } = this.props;

        const styles = cx(Styles, {
            [Kit.completed]: completed,
        });

        const currentMessage = editing ? newMessage : message;

        return (
            <li className = { styles }>
                <div className = { Kit.content }>
                    <Checkbox
                        inlineBlock
                        checked = { completed }
                        className = { Kit.complete }
                        color1 = '#3B8EF3'
                        color2 = '#FFF'
                        onClick = { this._completeTask }
                    />
                    <input
                        disabled = { !editing }
                        ref = { this.input }
                        type = 'text'
                        value = { currentMessage }
                        onChange = { this._editNewMessage }
                        onKeyDown = { this._updateTask }
                    />
                </div>
                <div className = { Kit.actions }>
                    <Star
                        inlineBlock
                        checked = { favorite }
                        className = { Kit.setPriority }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._changePriority }
                    />
                    <Edit
                        inlineBlock
                        checked = { editing }
                        className = { Kit.edit }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._toggleEditingState }
                    />
                    <Remove
                        inlineBlock
                        className = { Kit.remove }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._removeTask }
                    />
                </div>
            </li>
        );
    }
}
