import desktop from './desktop.m.css';
import phonePortrait from './phone/phonePortrait.m.css';
import phoneLandscape from './phone/phoneLandscape.m.css';
import tabletPortrait from './tablet/tabletPortrait.m.css';
import tabletLandscape from './tablet/tabletLandscape.m.css';
import desktopS from './desktop/desktopS.m.css';
import desktopM from './desktop/desktopM.m.css';
import desktopL from './desktop/desktopL.m.css';
import desktopXL from './desktop/desktopXL.m.css';

const Kit = {};

const combine = (classNamesMap) => {
    delete classNamesMap.init;

    const classNamesKeys = Object.keys(classNamesMap);

    for (const className of classNamesKeys) {
        if (className in Kit) {
            const existingClassName = Kit[className];
            const newClassName = classNamesMap[className];

            Kit[className] = `${existingClassName} ${newClassName}`;

            continue;
        }

        Kit[className] = classNamesMap[className];
    }
};

combine({ ...desktop });
combine({ ...phonePortrait });
combine({ ...phoneLandscape });
combine({ ...tabletPortrait });
combine({ ...tabletLandscape });
combine({ ...desktopS });
combine({ ...desktopM });
combine({ ...desktopL });
combine({ ...desktopXL });

export default `
    ${desktop.init}
    ${phonePortrait.init}
    ${phoneLandscape.init}
    ${tabletPortrait.init}
    ${tabletLandscape.init}
    ${desktopS.init}
    ${desktopM.init}
    ${desktopL.init}
    ${desktopXL.init}
`;

export { Kit };
